import './index.css';
import React from 'react';
import ReactDOM from 'react-dom/client';
import App from './App';
import { BrowserRouter } from 'react-router-dom';  // Bleibe bei BrowserRouter

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <BrowserRouter basename="/Website_v2">  // basename = dein Repo-Name (für Sub-Pfade)
    <App />
  </BrowserRouter>
);